import { MapboxLayer } from '@deck.gl/mapbox';
import { ScatterplotLayer, ArcLayer } from '@deck.gl/layers';

import { Solution } from '../';

export class DeckGL {
    public solution: Solution;
    deckLayer: any;
    deckLayer2: any;
    map: mapboxgl.Map;
    constructor(solution: Solution) {
        this.solution = solution;
    }
    public initPost() {
        this.map = this.solution.mapboxWrapper.map;

        this.deckLayer = new MapboxLayer({
            id: 'my-scatterplot',
            type: ScatterplotLayer,
            data: [
                {position: [-74.5, 40], size: 10000},
                {position: [-74.5, 40.5], size: 5000},
                {position: [-74.6, 40.5], size: 5800},
                {position: [-74.7, 40.5], size: 100},
            ],
            // @ts-ignore
            getPosition: d => d.position,
            getRadius: d => d.size,
            getFillColor: d => [255, 140, 0],
            getLineColor: d => [255, 255, 0]
        });
        this.map.addLayer(this.deckLayer, 'waterway-label');



        const data = [
            { pickup: [-122.42, 37.8], dropoff: [-74.5, 40.5] },
            { pickup: [-122.43, 37.8], dropoff: [-122.42, 37.75] }
        ];


        this.deckLayer2 = new MapboxLayer({
            id: 'my-scatterplot2',
            type: ArcLayer,
            data: data,
            // @ts-ignore
            getSourcePosition: d => d.pickup,
            getTargetPosition: d => d.dropoff,
            getSourceColor: d => [255, 128, 0],
            getTargetColor: d => [0, 128, 255],
            strokeWidth: 5
          });

        // this.deckLayer2 =  new ArcLayer({
        //     data,
        //     // @ts-ignore
        //     getSourcePosition: d => d.pickup,
        //     // @ts-ignore
        //     getTargetPosition: d => d.dropoff,
        //     getSourceColor: d => [255, 128, 0],
        //     getTargetColor: d => [0, 128, 255],
        //     strokeWidth: 5
        // });
   
        this.map.addLayer(this.deckLayer2 ); // 'water-shadow'
    }
}