import { Solution } from '../';
export class UIWrapper {
    public solution: Solution;
    public divHost: HTMLElement;
    constructor(solution: Solution) {
        this.solution = solution;
        this.divHost = solution.divHost;
        this.initUI();
    }
    public initUI() {
        const divMenu = document.createElement('div');
        this.divHost.appendChild(divMenu);

        const div = document.createElement('div');
        div.textContent = 'download';
        div.style.position = 'relative';
        div.style.margin = '10px';
        div.onclick = () => {
            this.solution.mapboxWrapper.downloadImage();
        }
        divMenu.appendChild(div);
    }
}