import { MapboxGLWrapper } from './MapboxGLWrapper';
import { DeckGL } from './DeckGL';
import { Renderer } from './CanvasCustom';
import { UIWrapper } from './UI';

export class Solution {
    public divHost: HTMLDivElement;
    public mapboxWrapper: MapboxGLWrapper;
    public deckGL: DeckGL
    public renderer: Renderer;
    public uiWrapper: UIWrapper;
    constructor(id: string) {
        this.divHost = document.getElementById(id) as HTMLDivElement;
        this.divHost.style.position = 'relative';
        
        this.deckGL = new DeckGL(this);
        this.mapboxWrapper = new MapboxGLWrapper(this);
        this.mapboxWrapper.mouseDown = this.mouseDown
        this.mapboxWrapper.mouseUp = this.mouseUp
        this.mapboxWrapper.mouseClick = this.mouseClick
        this.mapboxWrapper.mouseMove = this.mouseMove
        
        this.renderer = new Renderer(this.divHost, this);

        this.uiWrapper = new UIWrapper(this);
    }
    public initPost() {
        this.renderer.initPost();
        this.deckGL.initPost();
    }
    public mouseDown = (e) => {
        // TODO
    }
    public mouseUp = (e) => {
        // TODO
    }
    public mouseDrag = (e) => {
        // TODO
    }
    public mouseClick = (e) => {
        // TODO
        // console.log(e);
        // console.log(e['lngLat']);
        this.renderer.pts.push([e['lngLat']['lng'], e['lngLat']['lat']])
        // this.renderer.pts.push([e.point.x, e.point.y])
    }
    public mouseMove = (e)  =>{
        // TODO
    }
    public keyDown(k: KeyboardEvent) {
        // TODO
    }
    public destroy() {
        this.renderer.destroy();
        while (this.divHost.lastElementChild) {
            this.divHost.removeChild(this.divHost.lastElementChild);
        }
    }
}