    public initCustomCanvas(){

        this.solution.renderer.canvas.width = this.canvasMapbox.width ;
        this.solution.renderer.canvas.height = this.canvasMapbox.height ;

        // this.solution.renderer.canvas.style.width = '100%';
        // this.solution.renderer.canvas.style.height = '100%';
        this.solution.renderer.canvas.style.display = 'none';

       const bound: any = this.map.getBounds();
        // console.log(bound);

       this.map.addSource('canvas-source', {
            type: 'canvas',
            canvas: this.solution.renderer.canvas,
            coordinates: [

                // [ 180, 90],
                // [ 180, -90],
                // [ -180, -90], 
                // [ -180, 90]

                
                // [bound._sw[0], bound._sw[1]],
                // [bound._sw[0], bound._ne[1]],
                // [bound._ne[0], bound._ne[1]],
                // [bound._ne[0], bound._sw[1]],

               [ -125.618336, 48.944262], 
               [ -125.618336, 24.786775], 
               [ -65.591812, 24.786775],
               [ -65.591812, 48.944262]

                // [91.4461, 21.5006],
                // [100.3541, 21.5006],
                // [100.3541, 13.9706],
                // [91.4461, 13.9706]
            ],
            // Set to true if the canvas source is animated. If the canvas is static, animate should be set to false to improve performance.
            animate: true
            });
             
        this.map.addLayer({
            id: 'canvas-layer',
            type: 'raster',
            source: 'canvas-source'
            });

    }
    
    public center = (lng: number = -99.292649, lat: number = 39.043903) => {
        const v = new mapboxgl.LngLat(lng, lat);
        // console.log(v);
        this.map.setCenter({ lat, lng })
    }
    public toMap = (x: number, y: number) => {
        const p = this.map.unproject({x, y} as any)
        return [p.lng, p.lat];
    }
    public toScreen = (lon: number, lat: number) => {
        const p = this.map.project([lon, lat])
        return [p.x, p.y];
    }
