
export const downloadImage = (canvasMapbox, customCanvas, fileName = 'map.png') => {
    // const canvasMapbox = this.map.getCanvas(); // this.solution.deckGL.deckLayer.deck.canvas; // this.map.getCanvas(); //  document.querySelector(".mapboxgl-canvas") as HTMLCanvasElement;

    let canvasOut = document.createElement("canvas");
    canvasOut.width = canvasMapbox.width;
    canvasOut.height = canvasMapbox.height;
    let context = canvasOut.getContext("2d");

    // context.globalAlpha = 1.0;
    context.globalAlpha = 1.0;
    // context.drawImage(this.deckLayer.deck.canvas, 0, 0);
    context.drawImage(canvasMapbox, 0, 0);
    // context.drawImage(this.solution.renderer.canvas, 0, 0, canvasMapbox.width, canvasMapbox.height);
    context.drawImage(customCanvas, 0, 0, canvasMapbox.width, canvasMapbox.height);

    let img = new Image();
    img.width = canvasMapbox.width;
    img.height = canvasMapbox.height;
    img.src = canvasOut.toDataURL();

    let link = document.createElement('a');
    link.download = fileName;
    link.href = canvasOut.toDataURL();
    link.click();    
};
