import { RendererCanvas } from '../../../../lib';
import { Solution } from '../';

export class Renderer extends RendererCanvas {
    public app: Solution
    public time: number = 0.0;
    public pts: number[][] = [];
    public isActive = false;
    constructor(div: any, app: Solution) {
        super(div);
        this.app = app;
        this.canvas.id = 'mapboxCustom'

        // const dd = app.mapboxWrapper.map.getCanvas();

        // this.canvas.width = dd.width;
        // this.canvas.height = dd.height;
        this.canvas.style.position = 'absolute';
        // this.canvas.style.width = '100%';
        // this.canvas.style.height = '100%';
        this.canvas.style.pointerEvents = 'none';
        this.canvas.style.mixBlendMode = 'color-dodge'

        // console.log(dd.width, dd.height,)
        // TODO
        // You code goes here for one time operation
        // this.pts.push([0, 0]); 
        // this.pts.push([this.canvas.width * 2, this.canvas.height * 2]); 
        // this.pts.push([-71.093161, 42.358871]);

        this.start();
    }
    public initPost() {
        this.ctx.globalCompositeOperation = 'color-dodge';
        this.isActive = true;
        // 
    }
    public mouseDown = (x: number, y: number): void => {

    }
    public mouseUp = (x: number, y: number): void => {

    }
    public mouseDrag = (x: number, y: number): void => {

    }
    public render(ctx: CanvasRenderingContext2D): void {
        if (!this.isActive) { return; }
        this.time += 0.1;

        const z = this.app.mapboxWrapper.map.getZoom();
        const zoomFactor = Math.pow(2, z) * 0.1
        

        for(let i = 0 ; i < this.pts.length; ++i) {
            const pt = this.pts[i];
            const p = this.app.mapboxWrapper.toScreen(pt[0], pt[1]);
            // const p = this.app.mapboxWrapper.map.unproject({x: 10, y: 10})
    

            const size = 0.25 * zoomFactor;

            ctx.beginPath();
            ctx.arc(p[0], p[1], size + ((Math.sin(this.time + i) + 3) * zoomFactor ), 0, Math.PI * 2);
            ctx.closePath();
            ctx.fillStyle = 'rgba(0, 255, 0, 0.7)';
            ctx.fill();
        }

    }
}